    min-width: 300px;
    /* max-width: 300px; */
    /* width: 300px; */
    position: relative;
}
.style_response__g9kK8::before
{
    content: '';
    position: absolute;
    top: -2px;
    bottom: -2px;
    left: -2px;
    right: -2px;
    background-color: white;
    z-index: -1;
    border-radius: 12px;
}
.style_response__g9kK8::after
{
    content: '';
    position: absolute;
    top: -2px;
    bottom: -2px;
    left: -2px;
    right: -2px;
    background-color: white;
    z-index: -1;
    filter: blur(10px);
    background: linear-gradient(235deg , #89ff00 , #010615 , #00bcd4);
}
