.style_open__3aBQQ , .style_reject__34rIB
{
    margin-top: 10px;
    background-color: #00ffb6;
    padding: 10px;
    border-radius: 10px;
    font-weight: 700;
    cursor: pointer;
    font-size: 15px;
    position: relative;
    z-index: 1;
}
.style_open__3aBQQ::before , .style_reject__34rIB::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    transition: background-color 0.3s ease;
    border-radius: 10px;
}
.style_open__3aBQQ:hover::before , .style_reject__34rIB:hover::before
{
    background-color: rgba(0, 0, 0, 0.3);
}
