@import url('https://fonts.googleapis.com/css2?family=Lobster&display=swap');
.modalContentClass
{
    position: absolute;
    top: 50%;
    left: 50%;
    right: auto;
    bottom: auto;
    margin-right: -50%;
    transform: translate(-50%, -50%);
    border: none;
    padding: 0px;
    overflow: hidden;
    height: 100vh;
    width: 75%;
}
.deletePostContentClass
{
    position: absolute;
    top: 50%;
    left: 50%;
    right: auto;
    bottom: auto;
    margin-right: -50%;
    transform: translate(-50%, -50%);
    border: 2px solid orange;
    border-radius: 10px;
    padding: 20px;
    background-color: white;
}
.modalOverlayClass
{
    position: fixed;
    z-index: 2;
    right: 0;
    bottom: 0;
    left: 0;
    top: 0;
    background-color: rgba(255, 255, 255, 0.8);
}
.jobs
{
    padding-top: 90px;
    margin: 0 20px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 20px;
    font-family: "Lobster", sans-serif;
    font-weight: 400;
    font-style: normal;
}
.empty
{
    color: white;
    position: relative;
    top: 90px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: calc(100vh - 90px);
    font-size: 30px;
}
.jobs > div
{
    display: flex;
    gap: 50px;
    max-width: 100%;
    justify-content: left;
}
.job
{
    background: linear-gradient(135deg, rgb(255 213 0 / 10%), rgb(251 248 248 / 30%));
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    border:1px solid rgba(255, 255, 255, 0.18);
    box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.37);
    border-radius: 10px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 395px;
    height: min-content;
    flex-shrink: 0;
}
.jobs span
{
    font-weight: 500;
}
.title
{
    font-size: 35px;
    padding: 10px;
}
.description
{
    font-size: 20px;
    padding: 7px;
}
.user
{
    font-size: 24px;
    padding: 4px;
}
.container
{
    display: flex;
    justify-content: space-between !important;
    font-size: 18px;
    padding: 5px 0px;
    width: 100%;
}
.location
{
    font-size: 24px;
    padding: 4px;
}
.responses
{
    display: flex;
    align-items: center;
    overflow: auto;
    gap: 30px;
    width: 1050px;
    overflow-x: scroll;
    height: 384px;
}
.responses::-webkit-scrollbar
{
    height: 3px;
}
.responses::-webkit-scrollbar-thumb
{
    background-color: white;
    border-radius: 20px;
}
.response
{
    background-color: #060c21;
    height: 300px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    padding: 20px;
    min-width: 300px;
    /* max-width: 300px; */
    /* width: 300px; */
    position: relative;
}
.response::before
{
    content: '';
    position: absolute;
    top: -2px;
    bottom: -2px;
    left: -2px;
    right: -2px;
    background-color: white;
    z-index: -1;
    border-radius: 12px;
}
.response::after
{
    content: '';
    position: absolute;
    top: -2px;
    bottom: -2px;
    left: -2px;
    right: -2px;
    background-color: white;
    z-index: -1;
    filter: blur(10px);
    background: linear-gradient(235deg , #89ff00 , #010615 , #00bcd4);
}
.glass
{
    position: absolute;
    top: 0;
    left: 0;
    width: 50%;
    min-height: 100%;
    display: block;
    background-color: rgba(255, 255, 255, 0.1);
    pointer-events: none;
}
@import url('https://fonts.googleapis.com/css2?family=Jersey+10&display=swap');
.content
{
    color: white;
    overflow-y: scroll;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    gap: 10px;
    flex-direction: column;
    font-family: "Jersey 10", sans-serif;
    font-size: 25px;
    font-weight: 400;
    font-style: normal;
    height: 100%;
}
.content::-webkit-scrollbar
{
    width: 4px;
}
.content::-webkit-scrollbar-thumb
{
    background-color: white;
    border-radius: 10px;
}
.content span
{
    color: #00ffb6;
}
.open , .reject
{
    margin-top: 10px;
    background-color: #00ffb6;
    padding: 10px;
    border-radius: 10px;
    font-weight: 700;
    cursor: pointer;
    font-size: 15px;
    position: relative;
    z-index: 1;
}
.open::before , .reject::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    transition: background-color 0.3s ease;
    border-radius: 10px;
}
.open:hover::before , .reject:hover::before
{
    background-color: rgba(0, 0, 0, 0.3);
}
.delete
{
    position: absolute;
    color: orangered;
    top: 20px;
    right: 20px;
    font-size: 1.7rem;
    cursor: pointer;
}
.reject
{
    background-color: #fe4141;
    color: white;
    padding-right: 15px;
    padding-left: 15px;
}
.confirmation
{
    font-size: 24px;
    padding: 20px 0px 40px 0px;
}
.buttonContainer
{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
}
.yes
{
    background-color: #fe4141;
    width: 100px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    outline: none;
    border: none;
    color: white;
    font-size: 20px;
    cursor: pointer;
}
.no
{
    background-color: #05c405;
    width: 100px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    outline: none;
    border: none;
    color: white;
    font-size: 20px;
    cursor: pointer;
}
.loader
{
    position: relative;
    top: 90px;
    height: calc(100vh - 90px);
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
@media only screen and (width <= 911px)
{
    .jobs > div
    {
        flex-direction: column;
        align-items: center;
    }
    .responses{width: 100%;}
}
@media only screen and (width <= 503px)
{
    .job{width: 75cqi;}
    .title{font-size: 7cqi;}
    .description{font-size: 4cqi;}
    .user{font-size: 5cqi;}
    .container{font-size: 3.63cqi;}
    .location{font-size: 5cqi;}
    .response{min-width: 63cqi;}
    .modalContentClass{width: 95%;}
    .deletePostContentClass{width: 87%;}
}
@media only screen and (width <= 341px)
{
    .deletePostContentClass
    {
        width: 83%;
        padding: 5px;
        padding-bottom: 20px;
    }
    .confirmation{font-size: 16px;}
    .yes , .no
    {
        font-size: 14px;
        width: 70px;
        height: 30px;
    }
}